<template>
  <v-layout>
    <v-navigation-drawer
    clipped
    app
    temporary
    :value="drawer"
    width="150"
    hide-overlay
    stateless
    >
      <v-toolbar flat>
        <v-list>
          <v-list-tile>
            <v-list-tile-title class="title">
              Test
            </v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-divider></v-divider>
      <v-list dense class="pt-0">
        <v-list-tile v-for="(item, index) in items" :key="item.title" @click="set(index)">
          <v-list-tile-action>
            <v-icon>{{item.icon}}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{item.title}}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-flex xs12 offset-xs2 v-if="drawer">
      <user-inventory v-if="items[0].tag && user"/>
      <admin-inventory v-if="items[1].tag && admin"/>
      <item v-if="items[2].tag && admin"/>
      <assay v-if="items[3].tag"/>
    </v-flex>
    <v-flex xs12 v-else>
      <user-inventory v-if="items[0].tag && user"/>
      <admin-inventory v-if="items[1].tag && admin"/>
      <item v-if="items[2].tag && admin"/>
      <assay v-if="items[3].tag"/>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapState } from 'vuex'
import UserInventory from './UserInventory'
import AdminInventory from './AdminInventory'
import Item from './Item'
import Assay from './Assay'

export default {
  data () {
    return {
      items: [
        { title: 'User', icon: null, tag: false },
        { title: 'Admin', icon: null, tag: false },
        { title: 'Item', icon: null, tag: false },
        { title: 'Assay', icon: null, tag: false }
      ]
    }
  },
  components: {
    UserInventory,
    AdminInventory,
    Item,
    Assay
  },
  computed: {
    ...mapState([
      'user',
      'admin',
      'pageTitle',
      'drawer'
    ])
  },
  methods: {
    // find a smarter way to do this
    set (index) {
      for (let i = 0; i < this.items.length; i++) {
        if (i === index) {
          this.$store.dispatch('setTitle', this.items[i].title)
          this.$store.dispatch('setDrawer')
          this.items[i].tag = true
        } else {
          this.items[i].tag = false
        }
      }
    }
  }
}
</script>

<style scoped>

</style>



